<template>
    <div class="container col-md-4 mt-5">
        <form @submit.prevent="login()">
            <h2 class="my-2">login</h2>
            <hr>
            <label for ="email"> <b>Email </b> </label>
            <input type="text" placeholder="Enter Email" name ="Email" v-model="email" required>
            <label for ="Old Password"> <b>Password </b> </label>
            <input type="password" placeholder="Enter Password" name ="Enter Password" v-model="password" required>
            <div class ="clearfix">
            <button class ="changebtn"> Login </button>
            <p class="lead">if you don't have an account <routerLink to="/register">click here</routerLink></p>
            </div>

        </form>
    </div>
</template>

<script>
import axios from 'axios'
import {loginUser} from '../utils/auth'
    export default {
        name: 'login',
        data(){
            return{
                email:'',
                password:''
            }
        },
        methods:{
            async login(){
                 try {
                   await loginUser(this.email,this.password)
                   this.$router.push('/contact')
                }
            catch (err) {
                this.$swal("Error", err, "error")
            }
            }
        }
    }
</script>

<style scoped>
*{
box-sizing:border-box;
-webkit-box-sizing:border-box;
-moz-box-sizing:border-box;
-o-box-sizing:border-box;
}
.container{
padding:16px;
max-width:450px;
margin:auto;
background:linear-gradient(#787ea1,#000033);
background:-webkit-linear-gradient(#787ea1,#000033);
background:-moz-linear-gradient(#787ea1,#000033);
background:-o-linear-gradient(#787ea1,#000033);
color:white;
}
h1, p{
text-align:center;
}
input[type=text],input[type=password]{
width:100%;
padding:15px;
margin:5px 0 22px 0;
display:inline-block;
border:none;
background:#f1f1f1;
font-size:17px;
}
button{
background:#4caf50;
color:#fff;
padding:14px 20px;
margin:8px 0;
border:none;
cursor:pointer;
width:100%;
opacity:0.9;
font-size:17px;
}
button:hover{
opacity:1;
}
.cancelbtn{
background:#f44336;
}
.cancelbtn, .changebtn{
float:left;
