<template>
    <div>
        
    </div>
</template>

<script>
import {ProtectedData} from '../utils/message'
import {getPrivateData} from '../utils/message'
export default {
    data() {
        return {
            msg:'',
            tokens:'',
            Pdata:''
        }
    },
    methods: {
        async getMessage(){
            try{
            let token = localStorage.getItem('auth-token')
            this.tokens = token
            await ProtectedData(token)}
            catch(err){
                alert(`Erorr: ${err}`)
            }
        },
        async privateData(){
            this.Pdata = await getPrivateData()
        }
    },
}
</script>
