import axios from 'axios'

const REST_ENDPOINT = ''
const AUTH_TOKEN_KEY = 'auth-token'

export function ProtectedData(token) {
    return new Promise(async(resolve, reject) => {
        try {
            let response = await axios({
                url: `${REST_ENDPOINT}api/post`,
                method: 'GET',
                headers: {
                    'auth-token': token
                }
            })
            getPrivateData(response.data)
            resolve()
        } catch (error) {
            reject(error)
        }
    })
}
export function getPrivateData(data) {
    return data;
}