<template>
  <div class="container">
        <div class="">
      <form @submit.prevent="registerU">
        <h1>Register page</h1>
        <p>Please fill in this form to Register</p>
        <hr />
        <label for="name"> <b>Name </b> </label>
        <input
          type="text"
          placeholder="Enter name"
          name="name"
          v-model="name"
          required
        />
        <label for="email"> <b>Email </b> </label>
        <input
          type="text"
          placeholder="Enter email"
          name="email"
          v-model="email"
          required
        />

        <label for="password"> <b>Password </b> </label>
        <input
          type="Password"
          id="newp"
          placeholder="Enter  Password"
          v-model="password"
          name="password"
          required
        />

        <div class="clearfix">
          <button type="Submit" class="changebtn">Register</button>
          <p>
            Already have an account??<router-link to="/login"
              >click here</router-link
            >
          </p>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
  import { registerUser } from '../utils/register';
  export default {
    data() {
      return {
        name: '',
        email: '',
        password: '',
      };
    },
    methods: {
      async registerU() {
        try {
          await registerUser(this.name, this.email, this.password);
          this.$router.push('/login');
          this.$swal('Success', 'Registration Was successful', 'success');
        } catch (err) {
          this.$swal('Error', err, 'error');
        }
      },
    },
  };
</script>
<style scoped>
    * {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -o-box-sizing: border-box;
  }
  .container {
    padding: 16px;
    max-width: 450px;
    margin: auto;
    background: linear-gradient(#787ea1, #000033);
    background: -webkit-linear-gradient(#787ea1, #000033);
    background: -moz-linear-gradient(#787ea1, #000033);
    background: -o-linear-gradient(#787ea1, #000033);
    color: white;
  }
  h1,
  p {
    text-align: center;
  }
  input[type='text'],
  input[type='password'] {
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
    font-size: 17px;
  }
  button {
    background: #4caf50;
    color: #fff;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
    opacity: 0.9;
    font-size: 17px;
  }
  button:hover {
    opacity: 1;
  }
  .cancelbtn {
    background: #f44336;
  }
  .cancelbtn,
  .changebtn {
    float: left;
    width: 50%;
  }
  .back {
    text-align: left;
  }
  hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
  }
  .clearfix::after {
    content: '';
    clear: both;
    display: table;
  }
  @media screen and (max-width: 300px) {
    .cancelbtn,
    .changebtn {
      width: 100%;
    }
  }
</style>